<template>
	<view class="shop">
		<sandone v-if="sandstat"></sandone>
		<headone :title="shop.name" @fanhui="fanhui" @share="fenxiang"></headone>
		<huitopone v-if="headstat"></huitopone>
		<view class="shopbox" :style="{'top':topheight+'px','height':'calc(100vh - '+topheight+'px - '+domheight+'px )'}">
			<scrollone  :loadtopstat="loadtopstat" @refresherrefresh="refresherrefresh">
				<bannertwo :banner="shop.zhuimg"></bannertwo>
				<priceone :pricedata="dataone" @shouchang="shouchang" @fenxiang="fenxiang"></priceone>
				<textone @text="textbut">
					<view slot="text0" class="skutext">
						<view class="skutext-img">
							<image class="skutextimg" src="../../static/img/fenlei-select.png"></image>
						</view>
						<view class="skutext-val">共有6种型号可选</view>
					</view>
				</textone>
				<boxone title="宝贝评价" @boxbut="boxbut" v-if="pinlundata.length">
					<view slot="right" class="box-right-one">
						<p>查看全部</p>
						<image class="box-right-img" src="../../static/img/yjtselect.png"></image>
					</view>
					<pinlunone :pinlundata="pinlundata[0]"></pinlunone>
				</boxone>
				<imgone :imgdata="shop.xqimg"></imgone>
			</scrollone>
		</view>
		<tabartwo @gohome="gohome" @gocart="gocart" @gochat="gochat" @addcart="addcart" @gopay="gopay"></tabartwo>
	</view>
</template>

<script>
	import sandone from "../../components/sand/sandone/sandone.vue"
	import headone from "../../components/head/headone/headone.vue"
	import tabartwo from "../../components/tabar/tabartwo/tabartwo.vue"
	import scrollone from "../../components/scroll/scrollone/scrollone.vue"
	import huitopone from "../../components/huitop/huitopone/huitopone.vue"
	import bannertwo from "../../components/banner/bannertwo/bannertwo.vue"
	import priceone from "../../components/price/priceone/priceone.vue"
	import textone from "../../components/text/textone/textone.vue"
	import boxone from "../../components/box/boxone/boxone.vue"
	import pinlunone from "../../components/pinlun/pinlunone/pinlunone.vue"
	import imgone from "../../components/img/imgone/imgone.vue"
	export default {
		name:"shop",
		data() {
			return {
				sandstat:false,//页面加载遮幕
				resum:0,//当前刷新数
				loginsum:4,//总刷新数
				headstat:false,//头部导航显示隐藏
				topheight:45,//滚动窗体距离头部值
				domheight:50,//滚动窗体距离底部值
				loadtopstat:false,//滚动条下拉加载状态
				
				id:0,//商品id
				shop:[],//商品数据
				dataone:{//楼层1数据
					name:"",
					price:0.00,
					oldprice:0.00,
					salesum:0,
					lasbel:[],
				},
				pinlundata:[],//评论数据
			}
		},
		components:{
			sandone,
			headone,
			tabartwo,
			scrollone,
			huitopone,
			bannertwo,
			priceone,
			textone,
			boxone,
			pinlunone,
			imgone
		},
		onLoad(option){
			this.id = option.id;
			this.getshopdata();
			this.getshopevaluate();
		},
		//组件创建完毕执行
		created(){
			/* #ifdef  MP-WEIXIN */
			this.topheight = uni.getSystemInfoSync().statusBarHeight+40;//顶部距离
			this.domheight = 0;//底部距离
			/* #endif */
		},
		methods: {
			//获取商品数据
			async getshopdata(){
				const res = await this.$myRequest({
					url:'/api/shop/getalonedata',
					data:{
						'id':this.id,
					},
				})
				console.log(res);
				this.dataone['name'] = res.data.name;
				this.dataone['price'] = res.data.price;
				this.dataone['oldprice'] = res.data.oldprice;
				this.dataone['label'] = JSON.parse(res.data.label);
				this.dataone['salesum'] = res.data.salesum;
				this.shop = res.data;
				// this.updata();//加载成功事件
			},
			//获取商品评论
			async getshopevaluate(){
				const res = await this.$myRequest({
					url:'/api/evaluate/getpagedata',
					data:{
						'id':this.id,
					},
				})
				this.pinlundata = res.data;
				console.log(res.data);//加载成功事件
			},
			//内容选择
			textbut(str){
				console.log(str);
			},
			//头部返回
			fanhui(){
				console.log(getCurrentPages());
				// history.back(-1);//浏览器返回
				uni.navigateBack({
				    delta: 1
				});
			},
			//收藏商品
			shouchang(){
				console.log("收藏商品")
			},
			//分享商品
			fenxiang(){
				console.log("分享商品")
			},
			//滚动条下拉刷新
			refresherrefresh(){
				
			},
			//查看评论
			boxbut(){
				console.log("查看评论")
			},
			//回到首页
			gohome(){
				uni.switchTab({
					 url: '/pages/index/index'
				})
			},
			//去购物车
			gocart(){
				// console.log(getCurrentPages());
				// uni.setStorageSync('shopid', this.id);//缓存商品id
				uni.navigateTo({
					 url: '/pages/cartcopy/cartcopy'
				})
			},
			//去客服页面
			gochat(){
				uni.switchTab({
					 url: '/pages/kefu/kefu'
				})
			},
			//加入购物车
			addcart(){
				if(!uni.move){
					uni.login({
					  provider: 'weixin',
					  success: function (loginRes) {
					    console.log(loginRes.authResult);
					  }
					});
					// var jweixin = require('../../common/js/wxlogin.js');
					// jweixin.ready(function(){
						
					// });
				}else{
					console.log("已登录");
					uni.move = null;
				}
			},
			//立即购买
			gopay(){
				
			}
		}
	}
</script>

<style lang="scss" scoped>
.shopbox{
	width: 100%;
	position: fixed;
	left: 0px;
}
.skutext{
	width: 100%;
	height: 26px;
	margin: 5px 0px;
	line-height: 26px;
	display: flex;
}
.skutext-img{
	flex: 1;
	overflow: hidden;
}
.skutext-img .skutextimg{
	width: 26px;
	height: 26px;
	margin-right: 5px;
}
.skutext-val{
	padding: 0px 5px;
	background: #eee;
	color: #999;
	font-family:"Microsoft YaHei";
	border-radius: 5px;
	font-size: $uni-text-font;
}
// 评论按钮
.box-right-one{
	color: $uni-color-color;
	font-size: $uni-span-font;
	display: flex;
	align-items: center;
}
.box-right-img{
	width: 10px;
	height: 10px;
	margin-left: 2px;
}
</style>
